body {
    background-color: rgba(0, 0, 0, 0.144); 
    color: aliceblue;
    font-family: 'Courier New', Courier, monospace;
    padding: 20px;
  }
  
  .container {
    border: 1px solid; /* Optional: to create a terminal window effect */
    padding: 10px;
    width: 900px; /* Adjust width as needed */
    height: 500px; /* Adjust height as needed */
    overflow-y: auto; /* Enable scrolling if content overflows */
    background-color: black;
    border-radius: 20px;
  }
  
  .input-line {
    display: flex; /* Align items in a row */
  }
  
  .prompt {
    margin-right: 5px; /* Space between prompt and input */
  }
  
  .terminal-input {
    background: black; /* Match terminal background */
    color: green; /* Match terminal text color */
    border: none; 
    outline: none; /* Remove outline */
    flex-grow: 1; /* Allow input to take remaining space */
    font-size: 18px;
    font-family: 'Courier New', Courier, monospace;
  }

.terminal h1 {
    color: black;
}

.output {
    margin-bottom: 20px; /* Space at the bottom of the output */
}

.output div {
    margin-bottom: 10px; /* Space between each output line */
}

@media (max-width: 550px) {
  .container {
    width: 400px; /* Adjust width as needed */
    height: 500px;
  }  
}